body {
  margin: 0;
  font-family: "Indie Flower", cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

i {
  cursor: pointer;
  border: solid black 2px;
}

.lime {
  color: lawngreen !important;
}

.orange {
  background-color: rgb(255, 102, 0);
}

.col button {
  width: 150px;
  color: whitesmoke;
  margin-bottom: 5px;
}

.cardCont {
  float: left;
  position: relative;
}

.cardFront,
.cardBack {
  position: absolute;
  width: 100%;
  height: 100%;
  background: url("https://img.freepik.com/free-vector/black-white-hypnotic-background-abstract-seamless-pattern-illustration_118124-3765.jpg?size=626&ext=jpg");
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  -ms-backface-visibility: hidden;
}

.moreInfo {
  padding: 10px;
  color: white;
  line-height: 24px;
}

.game-cards-container {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  display: grid;
  justify-items: center;
  align-items: center;
  row-gap: 7px;
  background-color: "beige";
}

.hud {
  position: absolute;
  padding: 2px;
  left: 0%;
  top: 0%;
  z-index: 2;
}
